.banner 
{
    position: relative;
    height: 448px;
    color: white;
    object-fit: contain;

}
.banner_content 
{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner_title 
{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
    
}

.banner_description 
{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    max-width: 360px;
    height: 80px; 

}

.banner--fadeBottom 
{
    height: 7.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37, 37, 37, 0.61), 
        #111
    );
}
.banner_button 
{
    

    background-color: rgba(51,51,51,.5);
    border: none;
    border-radius: 10px;
    color: #fff;
    cursor: pointer;
    font-weight: 700;
    margin-right: 1rem;
    outline: none;
    padding: 0.5rem 2rem;
}
.banner_button:hover 
{
    color: black;
    background-color: white;

}